export default {

	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			timegoodsList: [],
			productList: [],
			grouponList: [],
			newGoodsList: [],
			floorGoodsList: []
		};
	},

	onLoad() {
		this.loadData();
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			// let carouselList = await this.$api.json('carouselList');
			// this.titleNViewBackground = carouselList[0].background;
			// this.swiperLength = carouselList.length;
			// this.carouselList = carouselList;

			// let goodsList = await this.$api.json('goodsList');
			// this.goodsList = goodsList || [];

			uni.request({
				url: 'https://www.xinghuohan.cn/xhh-wx-api/wx/home/index',
				method: 'GET',
				data: {},
				success: res => {
					let listresult = res.data.data.banner;
					this.titleNViewBackground = listresult[0].background;
					this.swiperLength = listresult.length;
					this.carouselList = listresult;
					// 类别
					this.productList = res.data.data.channel;
					// 限时秒杀
					let timegoodsList = res.data.data.hotGoodsList;
					this.timegoodsList = timegoodsList || [];
					// 团购
					let grouponList = res.data.data.grouponList;
					this.grouponList = grouponList || [];
					// 猜你喜欢
					let newGoodsList = res.data.data.newGoodsList;
					this.newGoodsList = newGoodsList || [];
					// 分类精选
					let floorGoodsList = res.data.data.floorGoodsList;
					this.floorGoodsList = floorGoodsList || [];
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		//详情页
		navToDetailPage(item) {
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		//商品类别列表页面（点击首页商品类别时触发）
		navToProductPage(product) {
			let id = product.id;
			this.$api.msg('点击了'+product.name+'--id='+id);
			uni.navigateTo({
				url: `/pages/category/category?cid=`+id
			})
		},
	},

























}
